.button-send {
  display: flex;
  justify-content: center;
  margin: 5px;
}

.button-send button {
  all: unset;
  width: 100%;
  height: 40px;
  background-color: #ffefbae1;
  border-radius: 20px;
  text-align: center;
}

.button-tap {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFEFBA;
  position: absolute;
  bottom: 90px;
  left: 50%;
  transform: translateX(-50%);
  padding: 20px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  transition: transform 0.05s ease;
}

.button-tap svg {
  width: 200px;
  height: 200px;
  user-select: none;
}

.button-tap:active {
  width: 190px;
  height: 190px;
}
  