.boosts-container {
    justify-content: center;
    text-align: center;
    align-items: center;
    margin: auto;
}

.boosts-h1 {
    color: rgb(255, 228, 138);
    font-size: 60px;
    margin-top: 10px;
    margin-bottom: 30px;
}

.boosts-content {
    justify-content: center;
    margin: auto;
    border-radius: 20px;
    max-width: 500px;
    background-color: bisque;
    text-align: start;
    padding: 20px;
}

.img-time,
.img-capacity {
  display: flex;
  align-items: center;
}

.container-svg-main svg {
  width: 100px;
  height: 100px;
  margin-right: 10px;
}

.cursor-conent svg {
    width: 20px;
    height: 20px;
}

.cursor-conent {
    margin-left: auto;
}