.footer-container {
  position: fixed;
  left: 20px; 
  right: 20px; 
  bottom: 10px; 
  background-color: #FFEFBA;
  border-radius: 20px;
  box-sizing: border-box;
}

.footer-nav {
  display: flex;
  justify-content: space-between;
  text-align: center;
  padding: 4px;
}

.mission svg,
.boosts svg,
.profile svg,
.home svg {
  max-width: 34px;
  max-height: 34px;
}

.mission,
.boosts,
.profile,
.home {
  text-decoration: none;
  color: black;
  flex: 1;
  display: flex; 
  flex-direction: column; 
  align-items: center;
  text-align: center; 
  margin: 0 10px;
  cursor: pointer;
  user-select: none;
}

.mission p,
.boosts p,
.profile p,
.home p {
  font-size: 12px;
}